import {Component, Input, OnInit} from '@angular/core';
import {Observable, Subject, Subscription} from 'rxjs';
import {RequestStatus} from '../../shared/request-status.enum';

// Handles idle - loading - failure cases of an observable & display corresponding UI for it
// Improvement idea : start-idle state controled by @Input(), could be the equivalent of a "see more" button
// COuld even be controlled by parent
/*
 Example usage :
 <!--
 <app-request-status-display [requestStatus]="listeningHistoryRequestStatus" (retryRequest)="retry()"></app-request-status-display>
 <app-music-list fxFlex [musics]="listeningHistory"></app-music-list>
 -->
 */
@Component({
  selector: 'app-observable-content',
  templateUrl: './observable-data-handler.component.html',
  styleUrls: ['./observable-data-handler.component.scss']
})
export class ObservableDataHandlerComponent implements OnInit {

  @Input() observable: Observable<any>;
  private dataSubject: Subject<any> = new Subject<any>();
  private subscription: Subscription;
  subscriptionStatus: RequestStatus = RequestStatus.IDLE;
  private error: any;

  public get observableData() {
    return this.dataSubject;
  }

  constructor() {
  }

  ngOnInit() {
    this.initSubscription();
  }

  initSubscription() {
    this.subscriptionStatus = RequestStatus.LOADING;
    this.subscription = this.observable.subscribe(data => {
        this.dataSubject.next(data);
        this.subscriptionStatus = RequestStatus.SUCCESS;
      },
      error => {
        this.error = error;
        this.subscriptionStatus = RequestStatus.FAILURE;
      });
  }

  retry() {
    this.subscription.unsubscribe();
    this.subscriptionStatus = RequestStatus.IDLE;
    this.initSubscription();
  }

}



