import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {TimelineComponent} from './timeline.component';
import {PageExploreComponent} from './page-explore/page-explore.component';
import {PageLibraryComponent} from './page-library/page-library.component';

const TimelineRoutes: Routes = [
  {path: '',  component: TimelineComponent,
    children: [
      {path: '', redirectTo: 'explore', pathMatch: 'prefix'},
      {path: 'explore',  component: PageExploreComponent },
      {path: 'library',  component: PageLibraryComponent }
    ]},
];

@NgModule({
  imports: [
    RouterModule.forChild(TimelineRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class TimelineRoutingModule {}



