import {Component, OnInit} from '@angular/core';
import {BrowserInfosService} from '../../core/browser-infos.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-extension-checker',
  templateUrl: './extension-checker.component.html',
  styleUrls: ['./extension-checker.component.scss']
})
export class ExtensionCheckerComponent implements OnInit {

  needsToAppear = false;

  constructor(private browserInfos: BrowserInfosService, private router: Router) {}

  ngOnInit() {

    if ( this.browserInfos.isBrowserCompatibleWithExtension() ) {
      this.browserInfos.isExtensionLoaded().then(isExtensionLoaded => {

        if ( !isExtensionLoaded ) {
          this.needsToAppear = true;
        }

      }).catch(error => {
        // In case of error, bar stays hidden. Better than showing it by mistake
      });
    } else {
    // Browser not compatible with extension
    }
  }

  installExtension() {
    this.hideBar();
    window.open('https://chrome.google.com/webstore/detail/timeline/khnipnegdfealajagefmbfgdmdcgmhjd?hl=fr', '_blank');
  }

  hideBar() {
    this.needsToAppear = false
  }

}



