import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {TimelineRoutingModule} from './timeline-routing.module';

import {TimelineComponent} from './timeline.component';
import {SharedModule} from 'app/shared/shared.module';
import {PageLibraryComponent} from './page-library/page-library.component';
import {PageExploreComponent} from './page-explore/page-explore.component';
import {MusicListComponent} from './music-list/music-list.component';
import {FlexLayoutModule} from '@angular/flex-layout';
import {AlbumViewComponent} from './album-view/album-view.component';
import {LibraryService} from './page-library/library.service';
import {TimelineNavbarComponent} from './timeline-navbar/timeline-navbar.component';
import {
  MatButtonModule, MatCardModule, MatDialogModule, MatIconModule, MatInputModule, MatMenuModule,
  MatProgressSpinnerModule, MatSidenavModule, MatTabsModule, MatToolbarModule, MatTooltipModule
} from '@angular/material';
import {UiComponentsModule} from '../ui-components/ui-components.module';
import {WelcomeDialogComponent} from './welcome-dialog/welcome-dialog.component';
import {ExtensionCheckerComponent} from './extension-checker/extension-checker.component';
import {BrowserInfosService} from '../core/browser-infos.service';
import {ExploreService} from './page-explore/explore.service';
import {MusicCardComponent} from './deprecated/music-card/music-card.component';
import {MusicCardCarrousselComponent} from './deprecated/music-card-carroussel/music-card-carroussel.component';
import {FormsModule} from '@angular/forms';
import {ListeningSessionsListComponent} from './listening-sessions/listening-sessions-list.component';
import {ListeningSessionsService} from './listening-sessions/listening-sessions.service';
import {ListeningSessionComponent} from './listening-session/listening-session.component';
import {MusicHistoryComponent} from './music-history/music-history.component';
import {TimelineSidenavComponent} from './timeline-sidenav/timeline-sidenav.component';

@NgModule({
  imports: [
    CommonModule,
    TimelineRoutingModule,
    SharedModule,
    FlexLayoutModule,
    MatToolbarModule,
    MatButtonModule,
    MatDialogModule,
    MatTabsModule,
    MatProgressSpinnerModule,
    MatIconModule,
    MatCardModule,
    MatTooltipModule,
    UiComponentsModule,
    FormsModule,
    MatInputModule,
    MatSidenavModule,
    MatMenuModule
  ],
  declarations: [
    TimelineComponent,
    PageLibraryComponent,
    PageExploreComponent,
    MusicListComponent,
    AlbumViewComponent,
    TimelineNavbarComponent,
    WelcomeDialogComponent,
    ExtensionCheckerComponent,
    MusicCardComponent,
    MusicCardCarrousselComponent,
    ListeningSessionsListComponent,
    ListeningSessionComponent,
    MusicHistoryComponent,
    TimelineSidenavComponent
  ],
  exports: [
    TimelineSidenavComponent
  ],
  providers: [LibraryService, BrowserInfosService, ExploreService, ListeningSessionsService],
  entryComponents: [WelcomeDialogComponent]
})
export class TimelineModule {}



