import {Component, OnInit} from '@angular/core';
import {Music} from '../api/music';
import {ListeningSession} from '../api/listening-session';
import {Observable, Subject} from 'rxjs';
import {RequestStatus} from '../../shared/request-status.enum';
import {ListeningSessionsService} from './listening-sessions.service';

@Component({
  selector: 'app-listening-sessions-list',
  templateUrl: './listening-sessions-list.component.html',
  styleUrls: ['./listening-sessions-list.component.scss']
})
export class ListeningSessionsListComponent implements OnInit {

  constructor(public listeningSessionsService: ListeningSessionsService) {
  }

    listeningSessionsStream: Subject<Array<ListeningSession>> = new Subject();
    loadMoreListeningSessionsStream: Subject<any> = new Subject();
    listeningSessionRequestStream: Observable<Date>;
    listeningSessionRequestStatus: RequestStatus = RequestStatus.IDLE;
    private lastListeningSession: ListeningSession = void 0;
    private numberOfListeningSessionsPerRequest: number = 5;
    hasLoadedEverything: boolean = false;
    currentSessions: Array<ListeningSession> = [];

    ngOnInit() {

      this.listeningSessionsStream.subscribe(sessions => {
        sessions.forEach(session => this.currentSessions.push(session));
      })

      this.listeningSessionRequestStream = this.loadMoreListeningSessionsStream.asObservable()
        .map(() => {
          if (this.lastListeningSession !== void 0) {
            return this.lastListeningSession.startDate;
          } else {
            return new Date(Date.now());
          }
        });

      this.listeningSessionRequestStream.subscribe((startDate: Date) => {
        if ( this.hasLoadedEverything ) {
          return;
        }

        this.listeningSessionRequestStatus = RequestStatus.LOADING;
        this.listeningSessionsService.listeningSessionsByDescendingDate(startDate, this.numberOfListeningSessionsPerRequest).subscribe(sessions => {

          if ( sessions.length === 0 ) {
            this.hasLoadedEverything = true;
            this.listeningSessionRequestStatus = RequestStatus.SUCCESS;
          } else {
            this.lastListeningSession = sessions.slice(-1).pop();
            this.listeningSessionsStream.next(sessions);
          }
        }, (error) => {
          this.listeningSessionRequestStatus = RequestStatus.FAILURE;
        });
      });

      this.listeningSessionsStream.subscribe(_ => this.listeningSessionRequestStatus = RequestStatus.IDLE);

      // Start with something
      this.loadMore();
    }

  loadMore() {
    this.loadMoreListeningSessionsStream.next();
  }



}



