import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { AuthService } from '../../core/auth.service';
import { environment } from './../../../environments/environment';
import { Music } from '../api/music';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { MusicDomain } from '../api/music-domain.enum';
import { ListeningSession } from '../api/listening-session';
import { TimelineUtils } from '../timeline-utils';


@Injectable()
export class ListeningSessionsService {

  constructor(private http: Http, private auth: AuthService) {
  }

  listeningSessionsByDescendingDate(date: Date, number = 3): Observable<Array<ListeningSession>> {
    const headers = new Headers({ 'Authorization': this.auth.authorizationHeaderValue });
    headers.append('Content-Type', 'application/json');
    const options = new RequestOptions({ headers: headers });
    return this.http.get(environment.servicesUrls.streamService + `listeningSession/byDescendingDate/${date.toISOString()}/${number}`, options)
      .map(res => res.json())
      .map(sessions => sessions.map(session => Object.assign({
        startDate: new Date(session.startDate),
        endDate: new Date(session.endDate),
        name: session.name,
        musics: session.musics.map(dirtyMusic => Object.assign({
          title: dirtyMusic.title,
          artist: dirtyMusic.artist,
          url: dirtyMusic.url,
          listenedDate: new Date(dirtyMusic.date),
          domain: TimelineUtils.handleDomain(dirtyMusic.domain)
        }))
        })
      ));
  }

}



