import {Component, Input, OnInit} from '@angular/core';
import {Music} from '../api/music';
import {MusicDomainEnumAware} from '../api/music-domain-enum-aware.decorator';
import {MusicDomain} from '../api/music-domain.enum';

@Component({
  selector: 'app-music-list',
  templateUrl: './music-list.component.html',
  styleUrls: ['./music-list.component.scss']
})
@MusicDomainEnumAware
export class MusicListComponent implements OnInit {

  @Input() musics: Array<Music> = [];

  constructor() {}

  ngOnInit() {
  }

  openTabWithMusic(url: string) {
    window.open(url, '_blank');
  }

  musicClass(music) {
    switch (music.domain) {
      case MusicDomain.YOUTUBE:
        return 'domain-youtube';

      case MusicDomain.SOUNDCLOUD:
        return 'domain-soundcloud';

      case MusicDomain.DEEZER:
        return 'domain-deezer';

      case MusicDomain.UNKNOWN:
        return 'domain-unknown';

      default:
        return 'domain-unknown';
    }

  }

}



