import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FlexLayoutModule} from '@angular/flex-layout';
import {GoBackToTimelineBarComponent} from './go-back-to-timeline-bar/go-back-to-timeline-bar.component';
import {ObservableDataHandlerComponent} from './observable-data-hander/observable-data-handler.component';
import {RequestStatusDisplayComponent} from './request-status-display/request-status-display.component';
import {UiSimpleViewComponent} from './ui-simple-view/ui-simple-view.component';
import {FooterComponent} from './footer/footer.component';
import {RouterModule} from '@angular/router';
