import {Component, OnInit} from '@angular/core';
import {RequestStatus} from '../../shared/request-status.enum';
import {ExploreService} from './explore.service';
import {Music} from '../api/music';

@Component({
  selector: 'app-present',
  templateUrl: './page-explore.component.html',
  styleUrls: ['./page-explore.component.scss']
})
export class PageExploreComponent implements OnInit {

  lastListenings: Array<Music> = [];
  lastListeningsRequestStatus: RequestStatus = RequestStatus.IDLE;
  lastListeningsObservable;

  constructor(public exploreService: ExploreService) {}

  ngOnInit() {
    this.lastListeningsRequestStatus = RequestStatus.LOADING;
    this.loadLastListenings();
  }

  loadLastListenings() {
    this.lastListeningsObservable = this.exploreService.lastListenings().subscribe(musics => {
        this.lastListeningsRequestStatus = RequestStatus.SUCCESS;
        this.lastListenings = musics;
        console.log('got last listenings musics:' + JSON.stringify(musics));
      },
      error => {
        console.log('error' + JSON.stringify(error));
        console.log(error);
        this.lastListeningsRequestStatus = RequestStatus.FAILURE;
      });
  }

  retryLoadLastListenings() {
    this.lastListeningsObservable.unsubscribe();
    this.loadLastListenings();
  }

}



