import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'concatStreams',
  pure: true
})
export class ConcatStreamsPipe implements PipeTransform {
  private allData: Array<any> = [];
  transform(values: Array<any>): any {
    if (values) {
      this.allData.push(...values);
      return this.allData;
    }
  }
}



