import {Component, OnInit} from '@angular/core';
import {NavbarItem} from './navbar-item';
import {Input} from '@angular/core';
import {NavbarItemLayout} from './navbar-item-layout.enum';

@Component({
    selector: 'app-navbar',
    templateUrl: 'navbar.component.html',
    styleUrls: ['navbar.component.scss']
})

export class NavbarComponent implements OnInit {

  private readonly defaultItems: Array<NavbarItem> =
    [{name: 'CONFIGURE', route: '', layout: NavbarItemLayout.LEFT},
      {name: 'YOUR', route: '', layout: NavbarItemLayout.MIDDLE},
      {name: 'NAVBAR-ITEMS', route: '', layout: NavbarItemLayout.RIGHT}
    ];

  private readonly  defaultTitle: string = 'No-Title app';


  @Input() navbarItems: Array<NavbarItem> = this.defaultItems;
    @Input() title: string = this.defaultTitle;


    ngOnInit(): void {
    }

    getLeftItems(): Array<NavbarItem> {
      return this.navbarItems.filter(item => item.layout === NavbarItemLayout.LEFT);
    }

    getRightItems(): Array<NavbarItem> {
      return this.navbarItems.filter(item => item.layout === NavbarItemLayout.RIGHT);
    }

  getMiddleItems(): Array<NavbarItem> {
    return this.navbarItems.filter(item => item.layout === NavbarItemLayout.MIDDLE);
  }

    // For fxLayoutAlign
  getFxLayoutAlignClass(navbarItem: NavbarItem) {
    console.log('fine');
    if ( navbarItem.layout === NavbarItemLayout.RIGHT ) {
      return 'end center';
    }
    else if  ( navbarItem.layout === NavbarItemLayout.LEFT ) {
      return 'start center';
    }
    else {
      console.error('unknown layout');
      return 'start center';
    }
    }

    getNavbarItemClasses(navbarItem: NavbarItem) {
        if ( navbarItem.layout === NavbarItemLayout.RIGHT ) {
            return 'rightLayout';
        }
        else if  ( navbarItem.layout === NavbarItemLayout.LEFT ) {
            return 'leftLayout';
        }
        else {
            console.error('unknown layout');
            return 'leftLayout';
        }
    }
}



