import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {AuthService} from '../../core/auth.service';

@Component({
  selector: 'app-page-showcase',
  templateUrl: './page-showcase.component.html',
  styleUrls: ['./page-showcase.component.scss']
})
export class PageShowcaseComponent implements OnInit {

  constructor(private router: Router) {}

  ngOnInit() {
  }

  musicNewsContent = 'No more following here and there, no more digging news. Timeline keeps you informed based on what you listen. Simple.';
  musicUnifiedContent = 'All your music life gathered in a single place. We unify your musics from all major listening platforms';
  musicMemoryContent = "What's best to remember the past than listening to it ? Every one of your sound memories is a click away with Timeline.";


  goToSubscribePage() {
    this.router.navigate(['account/signup']);
  }

  goToLoginPage() {
    this.router.navigate(['account/login']);
  }
}



