import {Component, Input, OnInit} from '@angular/core';
import {MatSidenav} from '@angular/material';
import {SidenavService} from '../../sidenav.service';

@Component({
  selector: 'app-timeline-sidenav',
  templateUrl: './timeline-sidenav.component.html',
  styleUrls: ['./timeline-sidenav.component.scss']
})
export class TimelineSidenavComponent implements OnInit {

  @Input() sidenav: MatSidenav;

  constructor(private sidenavService: SidenavService) {
  }

  ngOnInit() {
    this.sidenavService.sidenavOpened().subscribe(() => {
      this.sidenav.open();
    })
  }

}



