import {NgModule} from '@angular/core';
import {RouterModule, Routes, PreloadAllModules} from '@angular/router';
import {PageShowcaseComponent} from './page-showcase/page-showcase.component';
import {PageAboutComponent} from './page-about/page-about.component';
import {PageTermsOfServiceComponent} from './page-terms-of-service/page-terms-of-service.component';

const publicPagesRoutes: Routes = [
  {
    path: '',
    component: PageShowcaseComponent
  },
  {
    path: 'about',
    component: PageAboutComponent
  },
  {
    path: 'tos',
    component: PageTermsOfServiceComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(publicPagesRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class PublicPagesRoutingModule {}



