import { Component, OnInit, Input } from '@angular/core';
import {ListeningSession} from './../api/listening-session';

@Component({
  selector: 'app-listening-session',
  templateUrl: './listening-session.component.html',
  styleUrls: ['./listening-session.component.scss']
})
export class ListeningSessionComponent implements OnInit {

  @Input() listeningSession: ListeningSession = void 0;
  showTracks: boolean = false;

  constructor() { }

  ngOnInit() {

  }

  toggleList() {
    this.showTracks = !this.showTracks;
  }
}



