import { Component, OnInit } from '@angular/core';
import {RequestStatus} from '../../shared/request-status.enum';
import {LibraryService} from '../page-library/library.service';
import {Observable, Subject} from 'rxjs';
import {Music} from '../api/music';

interface DateMusics {
  musics: Array<Music>;
  date: Date;
}

@Component({
  selector: 'app-music-history',
  templateUrl: './music-history.component.html',
  styleUrls: ['./music-history.component.scss']
})
export class MusicHistoryComponent implements OnInit {

  listeningHistoryStream: Subject<Array<Music>> = new Subject();
  loadMoreListeningHistoryStream: Subject<any> = new Subject();
  listeningHistoryRequestStream: Observable<Date>;
  listeningHistoryRequestStatus: RequestStatus = RequestStatus.IDLE;
  private lastMusic: Music = void 0;
  private numberOfMusicsByRequest: number = 5;
  hasLoadedEverything: boolean = false;

  orderedByDayListeningHistory: Array<DateMusics> = [];

  constructor(public pastService: LibraryService) {
  }

  ngOnInit() {

    // Requests button
    this.listeningHistoryRequestStream = this.loadMoreListeningHistoryStream.asObservable()
      .map(() => {
        if (this.lastMusic !== void 0) {
          return this.lastMusic.listenedDate;
        } else {
          return new Date(Date.now());
        }
      });

    // Handle request stream
    this.listeningHistoryRequestStream.subscribe((startDate: Date) => {

      if ( this.hasLoadedEverything ) {
        return;
      }

      this.listeningHistoryRequestStatus = RequestStatus.LOADING;
      this.pastService.musicHistoryByDescendingDate(startDate, this.numberOfMusicsByRequest).subscribe(musics => {
        if ( musics.length === 0) {
          this.hasLoadedEverything = true;
          this.listeningHistoryRequestStatus = RequestStatus.SUCCESS;
        } else {
          this.lastMusic = musics.slice(-1).pop();
          this.listeningHistoryStream.next(musics);
        }
      }, (error) => {
        this.listeningHistoryRequestStatus = RequestStatus.FAILURE;
      });
    });

    this.listeningHistoryStream.subscribe(_ => this.listeningHistoryRequestStatus = RequestStatus.IDLE);


    // Fill up ordered list with raw stream
    this.listeningHistoryStream.subscribe(newMusics => {
      this.addMusicsToOrderedArray(newMusics)
    });



    // Start with something
    this.loadMore();
  }

  loadMore() {
    this.loadMoreListeningHistoryStream.next();
  }

  addMusicsToOrderedArray(newMusics: Array<Music>) {
    // Case array empty
    if ( this.orderedByDayListeningHistory.length === 0 ) {
      const firstMusicToAdd = newMusics[0];
      newMusics.shift();
      const newDailyMusics: DateMusics =  {
        musics: [firstMusicToAdd],
        date: firstMusicToAdd.listenedDate
      };
      this.orderedByDayListeningHistory.push(newDailyMusics);
    }

    // Sort by day algo
    let lastDailyMusicsDate = this.dateToDayString(this.orderedByDayListeningHistory[this.orderedByDayListeningHistory.length - 1].date);
    for (const newMusic of newMusics) {
      const newMusicDate = this.dateToDayString(newMusic.listenedDate);
      if ( lastDailyMusicsDate === newMusicDate ) {
        this.orderedByDayListeningHistory[this.orderedByDayListeningHistory.length - 1].musics.push(newMusic);
      } else {
        const newDailyMusics: DateMusics =  {
          musics: [newMusic],
          date: newMusic.listenedDate
        };

        this.orderedByDayListeningHistory.push(newDailyMusics);
        lastDailyMusicsDate = this.dateToDayString(newMusic.listenedDate);
      }
    }
  }


  dateToDayString(date: Date) {
    return date.toISOString().slice(0, 10).replace(/-/g, '');
  }
}



