import {Component, Input, OnInit} from '@angular/core';
import {Music} from '../../api/music';

@Component({
  selector: 'app-music-card-carroussel',
  templateUrl: './music-card-carroussel.component.html',
  styleUrls: ['./music-card-carroussel.component.scss']
})
export class MusicCardCarrousselComponent implements OnInit {

  @Input() readonly numberOfElementsPerPage: number = 3;
  @Input() readonly data: Array<Music> = [];

  private _lastListeningsOffset = 0;

  get lastListeningsOffset() {
    return this._lastListeningsOffset;
  }

  set lastListeningsOffset(newOffset: number) {
    if (newOffset < 0) {
      this._lastListeningsOffset = 0;
    } else if (newOffset >= this.data.length - 1 - this.numberOfElementsPerPage) {
      this._lastListeningsOffset = this.data.length - 1 - this.numberOfElementsPerPage;
    } else {
      this._lastListeningsOffset = newOffset;
    }
  }

  constructor() {}

  ngOnInit() {
  }

  next() {
    this.lastListeningsOffset += this.numberOfElementsPerPage;
  }

  previous() {
    this.lastListeningsOffset -= this.numberOfElementsPerPage;
  }

}



