import {Directive, ElementRef, EventEmitter, HostListener, Output} from '@angular/core';

@Directive({
  selector: '[appIsVisible]'
})
export class IsVisibleDirective {

  @Output() becameVisible: EventEmitter<boolean> = new EventEmitter<boolean>();
  private oldIsVisible = false;
  public isVisible = false;
  private el: ElementRef;

  constructor(el: ElementRef) {
    this.el = el;
  }

  private isElementInViewport (el: ElementRef) {

    const rect = this.el.nativeElement.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
      rect.right <= (window.innerWidth || document.documentElement.clientWidth)
    );
  }

  checkVisibility() {
    this.oldIsVisible = this.isVisible;
    this.isVisible = this.isElementInViewport(this.el);

    if ( this.isVisible && !this.oldIsVisible ) {
      this.becameVisible.emit(this.isVisible);
    }
  }

// TODO: throttle shits a bit
  @HostListener('window:scroll', ['$event'])
  onScroll () {
    this.checkVisibility();
  }

  @HostListener('window:resize', ['$event'])
  onResize () {
    this.checkVisibility();
  }


}



