import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {ErrorObservable} from 'rxjs/observable/ErrorObservable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import {SignRequest} from '../account/api/sign-request';
import {Headers, Http, Response} from '@angular/http';
import {environment} from '../../environments/environment';
import {SignResponse} from '../account/api/sign-response';
import {ErrorMessage} from '../shared/error-message';
import {CookieService} from 'ngx-cookie';

@Injectable()
export class AuthService {

  private _sessionToken: string = void 0;
  HEADER = {headers: new Headers({'Content-Type': 'application/json' }) };

  // store the URL so we can redirect after logging in
  // Leaving the possibility to override the default one
  private defaultRedirectUrl = '/timeline';
  private askedRedirectUrl: string = void 0;

  get redirectUrl(): string {
    if (this.askedRedirectUrl !== void 0) {
      const url = this.askedRedirectUrl;
      this.askedRedirectUrl = void 0;
      return url;
    } else {
      return this.defaultRedirectUrl;
    }
  }

  set redirectUrl(url: string) {
    this.askedRedirectUrl = url;
  }

  get isLoggedIn(): boolean {
    return (this.sessionToken !== void 0);
  }

  private get sessionToken(): string {
    return this._sessionToken;
  }

  private set sessionToken(sessionToken) {
    this._sessionToken = sessionToken;
    this.cookieService.put('token', sessionToken);
  }

  get authorizationHeaderValue(): string {
    return 'Bearer ' + this._sessionToken;
  }

  constructor(private http: Http, private cookieService: CookieService) {
    this.sessionToken = cookieService.get('token');
  }

  login(signRequest: SignRequest): Observable<string> {
    return this.http.post(environment.servicesUrls.sessionService + 'session', signRequest, this.HEADER)
               .map((response: Response) => {
                 const serverResponse1 = response.json() as SignResponse;
                 this.sessionToken = serverResponse1.token;
                 return serverResponse1;
               })
               .catch((errorResponse: Response) => {
                 switch (errorResponse.status) {
                   case 401:
                   case 403:
                     const body = errorResponse.json() as SignResponse;
                     return ErrorObservable.create(new ErrorMessage('Login Failed:' + body.error));
                   default:
                     return ErrorObservable.create(new ErrorMessage('unknown error:'
                       + errorResponse.statusText
                       + ':' + errorResponse.toString()));
                 }
               });
  }

  logout(): void {
    this.cookieService.remove('token');
    this.sessionToken = void 0;
  }

}



