import {Component, ElementRef, OnInit, ViewChild, HostListener} from '@angular/core';
import {AuthService} from '../../core/auth.service';
import {Router} from '@angular/router';
import {WelcomeDialogComponent} from '../welcome-dialog/welcome-dialog.component';
import {MatDialog} from '@angular/material';
import {SidenavService} from '../../sidenav.service';

@Component({
  selector: 'app-timeline-navbar',
  templateUrl: './timeline-navbar.component.html',
  styleUrls: ['./timeline-navbar.component.scss']
})
export class TimelineNavbarComponent implements OnInit {

  constructor(private authService: AuthService, private sidenavService: SidenavService , private router: Router, private dialog: MatDialog) {}

  ngOnInit() {
  }

  logOut() {
    this.authService.logout();
    // Get the redirect URL from our auth service
    // If no redirect has been set, use the default
    const redirect = '/';
    // Redirect the user
    this.router.navigate([redirect]);
  }

  openWelcomeDialog() {
    this.dialog.open(WelcomeDialogComponent);
  }

  openSidenav() {
    this.sidenavService.openSidenav();
  }
}



