import {Component, ElementRef, EventEmitter, HostListener, Input, OnInit, Output, ViewChild} from '@angular/core';

@Component({
  selector: 'app-ui-dropdown-menu',
  templateUrl: './ui-dropdown-menu.component.html',
  styleUrls: ['./ui-dropdown-menu.component.scss']
})
export class UiDropdownMenuComponent implements OnInit {

  @ViewChild('dropdownMenu') dropdownMenu: ElementRef;
  @ViewChild('dropdownButton') dropdownButton: ElementRef;
  @Input() elements: Array<string> = [];
  @Output() dropdownButtonClick: EventEmitter<number> = new EventEmitter<number>();
  showDropdown = false;

  constructor() {}

  ngOnInit() {
  }


  dropdownButtonTrigger() {
      this.showDropdown = !this.showDropdown;

  }

  @HostListener('document:click', ['$event'])
  onDocumentClick(e: MouseEvent) {
    if ( !((this.dropdownMenu.nativeElement).contains(e.target) || (this.dropdownButton.nativeElement).contains(e.target)) && this.showDropdown ) {
      this.closeDropdown();
    }
  }


  closeDropdown() {
    this.showDropdown = false;
  }

  onDropdownButtonClick(index) {
    console.log('dropdown click on' + index);
    this.dropdownButtonClick.emit(index);
  }

}



