import {Injectable} from '@angular/core';
import {BROWSER} from './browser.enum';
declare var chrome: any;
declare var InstallTrigger: any;
declare var window: any;

@Injectable()
export class BrowserInfosService {

  public browser: BROWSER = BROWSER.OTHER;
  // The ID of the extension we want to talk to.
  private readonly timelineChromeExtensionId = 'ddjdjlnhahphcmdbmgjlhcocgcghempa';

  constructor() {
    this.browser = this.getBrowser();
  }

  private getBrowser() {
    // Firefox 1.0+
    const isFirefox = typeof InstallTrigger !== 'undefined';
    // Chrome 1+
    const isChrome = !!window.chrome && !!window.chrome.webstore;

    if ( isChrome && !isFirefox ) {
      return BROWSER.CHROME;
    } else if ( isFirefox && !isChrome ) {
      return BROWSER.FIREFOX;
    } else {
      // We play it safe here
      return BROWSER.OTHER;
    }
  }

  isBrowserCompatibleWithExtension(): boolean {
    const browser = this.getBrowser();
    return ( browser === BROWSER.CHROME ||browser === BROWSER.FIREFOX );
  }

  // TODO: only for CHROME atm
  isExtensionLoaded(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      if ( this.browser === BROWSER.CHROME ) {

        // Make a simple request:
        chrome.runtime.sendMessage(this.timelineChromeExtensionId, {query: 'health'},
          function(response) {

            if ( response !== void 0 && response.health === 'OK' ) {
              resolve(true);
            } else {
              resolve(false);
            }
          });

      } else {
        reject('Not 100% sure its loaded'); // IF we're not 100% sure, we throw an error.
      }

    });
  }
}



